  /**
   * populate policy number
   */
  public async populatePolicyForm(data: any): Promise<any> {
    this.commonService.showLoading();
    let searchTypeDetail: number;

    if (this.formModel.policyData.selectAndSearchDetails.value === 'Chasis Number') {
      searchTypeDetail = 2
    } else if (this.formModel.policyData.selectAndSearchDetails.value === 'Plate Number') {
      searchTypeDetail = 3
    } else {
      searchTypeDetail = 1
    }

    const headers = {
      searchType: searchTypeDetail,
      searchValue: data
    }
    return firstValueFrom(this.claimsService.getPolicyDetails(headers)).then(async (result: any) => {
      if (result && result?.success && result.data != null) {
        this.shouldOpenPopup = true;
        await this.populateDropdowns();
        this.customerDetailsPopupData = result;
        this.commonService.hideLoading();
      } else {
        this.shouldOpenPopup = false;
        this.commonService.showInfoToast(5000, 'show-info | ' + 'Entered value does not exist in our database. Please check and enter again');
        this.commonService.hideLoading();
      }
    }).catch((err) => {
      this.commonService.hideLoading();
    });
  }

