      this.formModel.vehicleData.vehicleRegistrationNum = { ...this.formModel.vehicleData.vehicleRegistrationNum, value: result.vehicleMetaData?.plateNo, disabled: true };
      this.formModel.vehicleData.registrationDate = { ...this.formModel.vehicleData.registrationDate, value: registrationDate, disabled: true };
