      this.formModel.vehicleData.make = { ...this.formModel.vehicleData.make, selectedOption: { 'name': make }, disabled: true };
      this.formModel.vehicleData.year = {
        ...this.formModel.vehicleData.year, disabled: true,
        selectedOption: { 'name': year }
      };
      this.formModel.vehicleData.model = {
        ...this.formModel.vehicleData.model, selectedOption: { 'modelDescription': model },
        disabled: true
      };
      this.formModel.vehicleData.vehicleType = {
        ...this.formModel.vehicleData.vehicleType, selectedOption: { 'name': result.vehicleMetaData?.vehicleType },
        disabled: true
      };
      this.vehicleFormData.vehicleType = this.formModel.vehicleData.vehicleType.selectedOption.name;
