      this.registrationDate = result.data?.registrationDate;
      this.manufacturingDate = result.data?.manufacturingDate;
      this.sumInsured = result.data?.sumAssured;

      const { vinNumber, plateNumber, registrationDate, make, model, year, manufacturingDate, vehicleType } = result.data;
