  /**
  * 
  * @param searchtext 
  */
  async onClickSearchIcon(searchtext: any): Promise<void> {
    if (searchtext === '') {
      return;
    }
    if (this.status && this.status !== CLAIM_STATUS.Draft) {
      const message = this.translateService.instant('policy_warning_message');
      this.commonService.showToast(0, message);
    } else {
      if ((this.currentSelectedValue === 'Policy Number' && (this.formModel.policyData.policyNumber.value == this.currentSearchValue))
        || (this.currentSelectedValue === 'Chasis Number' && (this.formModel.vehicleData.vin.value == this.currentSearchValue))
        || (this.currentSelectedValue === 'Plate Number' && (this.formModel.vehicleData.vehicleRegistrationNum.value == this.currentSearchValue))) return;

      await this.populatePolicyForm(searchtext);

      if (!(this.customerDetailsPopupData && this.customerDetailsPopupData.data && this.shouldOpenPopup)) return;

      if (this.customerDetailsPopupData.data.inProgressClaim && this.customerDetailsPopupData.data.inProgressClaim !== null) {
        this.openExistingClaimDialogPopup(this.customerDetailsPopupData.data.inProgressClaim)
      } else {
        this.openCustomerDetailsPopup();
      }
    }
  }

  /**
  * 
  * @param searchtext 
  */
  async openCustomerDetailsPopup(): Promise<void> {
    const dialogRef = this.dialog.open(CustomerPolicyDetailsDialogComponent, {
      data: {
        policyNumber: this.customerDetailsPopupData.data.policyNo,
        make: this.customerDetailsPopupData.data.make,
        model: this.customerDetailsPopupData.data.model,
        chasisNum: this.customerDetailsPopupData.data.vinNumber,
        plateNum: this.customerDetailsPopupData.data.plateNumber,
        customerName: this.customerDetailsPopupData.data.policyHolderFirstName + ' ' + this.customerDetailsPopupData.data.policyHolderLastName,
      },
      width: '779px',
      maxWidth: '779px',
      height: 'auto',
      panelClass: 'operation-panel',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: async (response: any) => {
        if (response == true) {
          if (this.claimGuid != '0') {
            const resetDialogRef = this.commonService.openConfirmYesNoDialogBig('confirm_reset', 'reset_policy_message');
            resetDialogRef.afterClosed().subscribe((result) => {
              if (result == true) {
                this.setFormValues(this.customerDetailsPopupData);
                this.checkForFormValidation();
              }
            });
          } else {
            this.setFormValues(this.customerDetailsPopupData);
            this.checkForFormValidation();
          }
        }
      }
    });
  }

  //status thing
  /**
    Opens a dialog box for the reject claim operation component with the specified data and panel class.
    @returns {void}
  */
  openRejectClaimPopup(data?: any): void {
    const dialogRef = this.dialog.open(RejectClaimDialogComponent, {
      data: {
        'claimGuid': this.claimGuid
      },
      height: 'auto',
      panelClass: 'operation-panel',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if (response) {
          this.status = CLAIM_STATUS.Rejected;
        }
      }
    });
  }

  /**
  * Approve claim
  */
  approve(): void {
    const dialogRef = this.commonService.openConfirmYesNoDialogBig(this.translateService.instant('confirm_action'), this.translateService.instant('msg_approve_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimsService.updateClaimStatus(this.claimGuid, CLAIM_STATUS.Approved, CLAIM_STATUS.ClaimApproved).subscribe({
          next: (response) => {
            if (response) {
              this.status = CLAIM_STATUS.ClaimApproved;
            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }

