  /**
  * submitForApproval
  */
  submitForApproval(): void {
    const dialogRef = this.commonService.openConfirmYesNoDialogBig(this.translateService.instant('confirm_action'), this.translateService.instant('msg_submit_approve_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimsService.updateClaimStatus(this.claimGuid, CLAIM_STATUS.ClaimSubmittedForApproval).subscribe({
          next: (response) => {
            if (response) {
              this.status = CLAIM_STATUS.ClaimSubmittedForApproval;
            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }

  /**
    Opens a dialog box for the Existing Caim component with the specified data and panel class.
    @returns {void}
  */
  openExistingClaimDialogPopup(inProgressClaim): void {
    const dialogRef = this.dialog.open(ExistingClaimDialogComponent, {
      data: inProgressClaim,
      width: '565px',
      maxWidth: '565px',
      height: 'auto',
      panelClass: 'operation-panel',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if (response) {
          this.openCustomerDetailsPopup();
        } else {
          this.router.navigate([ '/claim/my-claim' ]);
        }
      }
    });
  }
