import { Injectable } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable, map, of } from 'rxjs';
import { ClaimsService } from 'src/app/services/claims/claims.service';
import { CommonService } from 'src/app/services/common/common.service';

@Injectable({
  providedIn: 'root'
})
export class ClaimComponentGuard implements CanActivate {

  claimGuid: string = '';
  /**
   * constructor
   * @param claimService 
   * @param router 
   */
  constructor(private claimService: ClaimsService, private commonService: CommonService, private router: Router, private route: ActivatedRoute) {
    //this.claimGuid = this.route.parent?.snapshot.paramMap.get('id');
  }

  /**
   * 
   * @param route 
   * @param state 
   * @returns 
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    const url: string = state.url;

    this.claimGuid = route.parent?.params['id'];

    return this.checkStatus(url);
  }
  

  /**
   * check status
   * @param url 
   * @returns 
   */
  checkStatus(url: string): Observable<boolean> {

    if (!this.claimGuid || this.claimGuid == '0') {
      return of(true); // No claimGuid, allow navigation
    }

    // Call your web API to fetch the status
    return this.claimService.getClaimDetail(this.claimGuid).pipe(
      map((response: any) => {
        if (response) {
          this.commonService.claimDetails.next(response)
          return true; // Allow navigation
        }
        return false; // Something went wrong, disallow navigation
      })
    );
  }
}
