import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatIconModule } from '@angular/material/icon';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { Route, RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { DxDateBoxModule } from 'devextreme-angular';
import { ErrorModule } from 'src/app/shared/error-page/error.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { WidgetsModule } from 'src/app/widgets/widgets.module';
import { PolicyDetailsComponent } from './policy-details/policy-details.component';
import { CommonLayoutComponent } from './common-layout/common-layout/common-layout.component';
import { ClaimDocumentsComponent } from './claim-documents/claim-documents.component';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { GoogleMapsModule } from '@angular/google-maps';
import { CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { ClaimEstimateComponent } from '../estimate/claim-estimate.component';
import { GarageDetailsComponent } from '../garages/garage-details/garage-details.component';
import { ClaimImagesComponent } from './claim-images/claim-images/claim-images.component';
import { LossDetailComponent } from './loss-detail/loss-detail.component';
import { CustomerDetailsViewComponent } from './policy-details/customer-details-view/customer-details-view.component';
import { DirectiveModule } from 'src/app/helper/directive/directive.module';
import { SettleClaimDialogComponent } from 'src/app/dialogs/settle-claim-dialog/settle-claim-dialog.component';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { ClaimComponentGuard } from 'src/app/guards/claim-component-guard/claim-component.guard';
import { MaterialModule } from 'src/app/material.module';
import { PipeModule } from 'src/app/helper/pipe.module';
import { NavigationGuard } from 'src/app/guards/navigation-guard/navigation-guard';
import { CustomerPolicyDetailsDialogComponent } from 'src/app/dialogs/customer-policy-details-dialog/customer-policy-details-dialog.component';
import { MsalGuard } from '@azure/msal-angular';
import { RegisteredGarageAssignmentActionComponent } from './third-party-registered-garages/registered-garage-assignment-action/registered-garage-assignment-action.component';
import { FraudDetectionComponent } from './fraud-detection/fraud-detection.component';

import { HistoryComponent } from 'src/app/shared/history/history.component';
import { RegisteredGarageRequestEstimateActionComponent } from './third-party-registered-garages/registered-garage-request-estimate-action/registered-garage-request-estimate-action.component';
import { ThirdPartyCommonLayoutComponent } from './third-party-registered-garages/common-layout/common-layout.component';
import { MatExpansionModule } from '@angular/material/expansion';
import { RegisteredGarageRequestInvoiceActionComponent } from './third-party-registered-garages/registered-garage-request-invoice-action/registered-garage-request-invoice-action.component';
import { VerifyOtpComponent } from './third-party-registered-garages/verify-otp/verify-otp.component';
import { NgOtpInputModule } from 'ng-otp-input';
import { GaragesListComponent } from '../garages/garages-list/garages-list.component';


const claimRoutes: Route[] = [
  {
    path: '',
    canActivate: [ ClaimComponentGuard ],
    component: PolicyDetailsComponent,
    canDeactivate: [ NavigationGuard ]
  }, {
    path: 'loss-detail',
    canActivate: [ ClaimComponentGuard ],
    component: LossDetailComponent,
    canDeactivate: [ NavigationGuard ]
  }, {
    path: 'document',
    component: ClaimDocumentsComponent,
    canActivate: [ ClaimComponentGuard ],
    resolve: { version: VersionControlResolver },
  }, {
    path: 'photos',
    component: ClaimImagesComponent,
    canActivate: [ ClaimComponentGuard ],
    resolve: { version: VersionControlResolver },
  }, {
    path: 'garages',
    component: GaragesListComponent,
    canActivate: [ ClaimComponentGuard ],
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'garages/:garageId/garage-details',
    canActivate: [ ClaimComponentGuard ],
    component: GarageDetailsComponent,
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'estimate',
    component: ClaimEstimateComponent,
    canActivate: [ MsalGuard ],
    loadChildren: () =>
      import('../estimate/estimate.module').then(
        m => m.EstimateModule
      )
  },
  {
    path: 'customer-view',
    component: CustomerDetailsViewComponent,
    resolve: { version: VersionControlResolver },
  },
];

@NgModule({
  declarations: [
    PolicyDetailsComponent,
    CommonLayoutComponent,
    ClaimDocumentsComponent,
    GaragesListComponent,
    ClaimImagesComponent,
    LossDetailComponent,
    CustomerDetailsViewComponent,
    SettleClaimDialogComponent,
    RegisteredGarageAssignmentActionComponent,
    CustomerPolicyDetailsDialogComponent,
    FraudDetectionComponent,
    RegisteredGarageRequestEstimateActionComponent,
    ThirdPartyCommonLayoutComponent,
    RegisteredGarageRequestInvoiceActionComponent,
    VerifyOtpComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(claimRoutes),
    WidgetsModule,
    SharedModule,
    // MatDatepickerModule,
    // MatIconModule,
    MaterialModule,
    ErrorModule,
    TranslateModule,
    DxDateBoxModule,
    // MatProgressSpinnerModule,
    GoogleMapsModule,
    DirectiveModule,
    ScrollingModule,
    MatExpansionModule,
    NgOtpInputModule,
    HistoryComponent,
    PipeModule
  ],
  exports: [
    RouterModule,
    // MatDatepickerModule,
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ],
  providers: [ NavigationGuard ],
})
export class ClaimManagementModule { }