import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { firstValueFrom } from 'rxjs';
import { CLAIM_STATUS, STATUS_TEXT_DICT } from 'src/app/config/constants/claims.constants';
import { RejectClaimDialogComponent } from 'src/app/dialogs/reject-claim-dialog/reject-claim-dialog.component';
import { viewClaimDetailsForm, viewPolicyDetailsForm } from 'src/app/helper/form/claim-form.helper';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ClaimsService } from 'src/app/services/claims/claims.service';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor/monitor.service';
@Component({
  selector: 'app-customer-details-view',
  templateUrl: './customer-details-view.component.html',
  styleUrls: [ './customer-details-view.component.scss' ]
})
export class CustomerDetailsViewComponent implements OnInit {
  public formModel?: viewClaimDetailsForm;
  public policyFormModel?: viewPolicyDetailsForm;

  public domainId: number;
  public objectId: number;
  public claimGuid: string;
  public claimDetails: any;
  public status: string = '';
  public roleName: string = '';
  public showEditDeleteButtonsClaim: boolean = true;
  public canShowApproveRejectButton: boolean = false;
  public userPermission: any;
  public claimStatus: any = CLAIM_STATUS;

  public rejectButtonModel: ChqWidgetsButtonModel = {
    label: 'reject',
    type: 'outline-danger',
  }

  public approveButtonModel: ChqWidgetsButtonModel = {
    label: 'approve',
    type: 'primary',
    onclick: this.approve.bind(this)
  }

  public settleClaimButtonModel: ChqWidgetsButtonModel = {
    label: 'settle_claim',
    type: 'primary',
    onclick: this.settleClaim.bind(this)
  }

  public findGarageButtonModel: ChqWidgetsButtonModel = {
    label: 'Edit',
    type: 'outline',
    icon: 'edit'
  }

  public submitForApprovalButtonModel: ChqWidgetsButtonModel = {
    label: 'Submit for Approval',
    type: 'primary',
    onclick: this.submitForApproval.bind(this)
  }

  public editButtonModel: ChqWidgetsButtonModel = {
    label: 'Edit Details',
    type: 'outline',
    onclick: this.editDetails.bind(this)
  }

  public statusTextDict = STATUS_TEXT_DICT;

  /**
   * constructor
   * @param router
   */
  constructor(private router: Router,
    private route: ActivatedRoute,
    public commonService: CommonService,
    private claimService: ClaimsService,
    private monitorService: MonitorService,
    private translateService: TranslateService,
    public dialog: MatDialog,
  ) {
    this.commonService.userProfileData.subscribe((res) => {
      if (res?.data) {
        const { userPermission } = res.data;
        this.userPermission = userPermission;
      }
    });
  }

  /**
   * @returns {void}
  */
  canEdit(): boolean {
    return [
      CLAIM_STATUS.Draft.toLowerCase(),
      CLAIM_STATUS.ClaimAuthenticated.toLowerCase(),
      CLAIM_STATUS.DetailsUpdated.toLowerCase(),
      CLAIM_STATUS.UploadDocuments.toLowerCase(),
      CLAIM_STATUS.UploadPhotos.toLowerCase(),
      CLAIM_STATUS.Submitted.toLowerCase(),
      CLAIM_STATUS.Assigned.toLowerCase(),
      CLAIM_STATUS.ReviewInProgress.toLowerCase(),
      CLAIM_STATUS.AssignedBack.toLowerCase(),
      CLAIM_STATUS.ApprovalReviewInProgress.toLowerCase(),
      CLAIM_STATUS.ClaimSubmittedForApproval.toLowerCase(),
    ].includes(this.status?.toLowerCase()?.trim());
  }

  /**
   * show approve reject buttons
   */
  showApproveRejectButtons(): boolean {
    if ((this.commonService.isThirdPartyExternalGarage && this.claimDetails?.claimStatus === CLAIM_STATUS.RepairEstimation)
      || (!this.commonService.isThirdPartyExternalGarage && this.claimDetails?.claimStatus === CLAIM_STATUS.EstimateApproved)
      || (!this.commonService.isThirdPartyExternalGarage && this.claimDetails?.claimStatus === CLAIM_STATUS.EstimateRejected)) {
      return true;
    }

    return false;
  }

  /**
   * editing permissions
   */
  editPermissions(): boolean {
    const validStatuses = [ CLAIM_STATUS.Submitted, CLAIM_STATUS.ClaimSubmittedForApproval ];

    if (validStatuses.includes(this.status)) {
      // if not manager
      if (!this.checkClaimPermission('claim.selfapproval')) {
        return false;
      }
    } else if (!this.canEdit()) {
      return false;
    }

    if (this.claimDetails?.assigneeId) {
      // if assignee is not user id then he cannot edit unless he is a manager.
      if (this.commonService.userId !== this.claimDetails?.assigneeId && !this.checkClaimPermission('claim.selfapproval')) {
        return false;
      }

    }
    return true;
  }

  /**
   * claim permission
   * @param permissionName 
   */
  checkClaimPermission(permissionName: string): boolean {

    const automotiveService = this.userPermission?.automotiveServices;
    const permissionList = automotiveService?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda claims')?.permissions;

    return this.commonService.hasPermission(permissionName, permissionList);
  }

  /**
     * on init
     */
  async ngOnInit(): Promise<void> {
    this.formModel = viewClaimDetailsForm();
    this.policyFormModel = viewPolicyDetailsForm();

    this.roleName = this.commonService.roleName;
    this.route.parent?.params?.subscribe(async (data) => {
      this.claimGuid = this.route.parent?.snapshot.paramMap.get('id');
      if (this.claimGuid !== '0') {
        await this.getClaimDetails();
        this.showEditDeleteButtonsClaim = this.editPermissions();
        this.canShowApproveRejectButton = this.showApproveRejectButtons();
      }
    });
  }

  /**
   * get details data
   */
  /**
   * @returns {void}
  */
  async getClaimDetails(): Promise<void> {
    this.commonService.showLoading();
    await firstValueFrom(this.claimService.getClaimDetail(this.claimGuid)).then((response) => {
      if (response) {
        this.claimDetails = response;
        this.status = this.claimDetails?.claimStatus;
        this.commonService.claimStatus.next(this.status);
        this.commonService.claimDetails.next(this.claimDetails);
        //if status is 'assigned' and the user is claim reviewer then it should be changed to 'Review In Progress'
        let newStatus = '';

        if (this.checkClaimPermission('claim.submitforapproval') && this.status === CLAIM_STATUS.Assigned) {
          newStatus = CLAIM_STATUS.ReviewInProgress;
        } else if (this.checkClaimPermission('claim.selfapproval') && this.status === CLAIM_STATUS.ClaimSubmittedForApproval) {
          newStatus = CLAIM_STATUS.ApprovalReviewInProgress;
        }

        if (newStatus != '') {
          this.claimService.updateClaimStatus(this.claimGuid, newStatus).subscribe({
            next: (response) => {
              if (response) {
                this.commonService.showInfoToast(3000, `Status set to ${newStatus}`);
                this.status = newStatus;
                this.commonService.claimStatus.next(this.status);
              }
            }
          });
        }

      }
      this.commonService.hideLoading();
    }).catch(() => {
      this.commonService.hideLoading();
    });
  }

  /**
    Opens a dialog box for the reject claim operation component with the specified data and panel class.
    @returns {void}
  */
  openRejectClaimPopup(data?: any): void {
    const dialogRef = this.dialog.open(RejectClaimDialogComponent, {
      data: {
        'claimGuid': this.claimGuid
      },
      height: 'auto',
      panelClass: 'operation-panel',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if (response) {
          this.status = CLAIM_STATUS.Rejected;
          this.commonService.claimStatus.next(this.status);
          this.canShowApproveRejectButton = this.showApproveRejectButtons();
        }
      }
    });
  }

  /**
   * Approve claim
   */
  approve(): void {
    const dialogRef = this.commonService.openConfirmYesNoDialogBig(this.translateService.instant('confirm_action'), this.translateService.instant('msg_approve_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimService.updateClaimStatus(this.claimGuid, 'Approved', 'Claim Approved').subscribe({
          next: (response) => {
            if (response) {
              this.status = CLAIM_STATUS.Approved;
              this.commonService.claimStatus.next(this.status);
              this.claimDetails.claimStatus = this.status;
              this.canShowApproveRejectButton = this.showApproveRejectButtons();
            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }

  /**
   * settleClaim
   */
  settleClaim(): void {
    const dialogRef = this.commonService.openConfirmYesNoDialogBig(this.translateService.instant('confirm_action'), this.translateService.instant('msg_settle_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimService.updateClaimStatus(this.claimGuid, 'Completed', 'Settled').subscribe({
          next: (response) => {
            if (response) {
              this.status = CLAIM_STATUS.Complete;
              this.claimDetails.claimStatus = this.status;
            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }

  /**
   * submitForApproval
   */
  submitForApproval(): void {
    const dialogRef = this.commonService.openConfirmationOkDialog(this.translateService.instant('confirm_action'), this.translateService.instant('msg_submit_approve_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimService.updateClaimStatus(this.claimGuid, 'ClaimSubmittedForApproval').subscribe({
          next: (response) => {
            if (response) {
              this.status = 'ClaimSubmittedForApproval';
              this.commonService.claimStatus.next(this.status);

            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }

  /**
 * edit Details
 */
  editDetails(): void {
    //this.claimDetails
    // Bug 25826: Claim Manager is able to edit Claims created from FNOL/File a Claim without assigning
    if ((this.checkClaimPermission('claim.selfapproval')
      && (this.commonService.claimWorkflowStage.includes('2Stage') || this.commonService.claimWorkflowStage.includes('3Stage'))
      && ([ CLAIM_STATUS.ClaimAuthenticated ].includes(this.status)))
    ) {
      const dialog = this.commonService.openConfirmYesNoDialogBig('', 'msg_manager_review');

      dialog.afterClosed().subscribe((data) => {
        if (data) {
          this.claimService.updateClaimStatus(this.claimGuid, CLAIM_STATUS.ApprovalReviewInProgress).subscribe({
            next: (response) => {
              if (response) {
                this.commonService.showInfoToast(3000, `Status set to ${CLAIM_STATUS.ApprovalReviewInProgress}`);
                this.status = CLAIM_STATUS.ApprovalReviewInProgress;
                this.commonService.claimStatus.next(this.status);
                this.router.navigate([ `/claim/claim-detail/${this.claimGuid}` ]);
              } else {
                this.router.navigate([ '/claim/my-claim' ]);
              }
            }
          });
        } else {
          this.router.navigate([ '/claim/my-claim' ]);
        }
      }
      );
    } else {
      this.router.navigate([ `/claim/claim-detail/${this.claimGuid}` ]);
    }
  }
}

