import { Component, OnInit, ViewChild, ViewChildren } from '@angular/core';
import { FormBuilder, FormControl, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { TranslateService } from '@ngx-translate/core';
import * as dayjs from 'dayjs';
import * as utc from 'dayjs/plugin/utc';
import { saveAs } from 'file-saver';
import { Observable, firstValueFrom, forkJoin, map } from 'rxjs';
import { CLAIM_STATUS } from 'src/app/config/constants/claims.constants';
import { RejectClaimDialogComponent } from 'src/app/dialogs/reject-claim-dialog/reject-claim-dialog.component';
import { DeactivationGuarded } from 'src/app/guards/navigation-guard/navigation-guard';
import { AddLossDetailsForm, newLossDetailsForm, newVehicleDetailsForm } from 'src/app/helper/form/claim-form.helper';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { IControlValue } from 'src/app/model/chq-widgets-input-model';
import { CalendarService } from 'src/app/services/calendar/calendar.service';
import { ClaimsService } from 'src/app/services/claims/claims.service';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor/monitor.service';
import { VehicleService } from 'src/app/services/vehicle/vehicle.service';
import { ChqInputComponent } from 'src/app/widgets/chq-input/chq-input.component';
dayjs.extend(utc)

@Component({
  selector: 'app-loss-detail',
  templateUrl: './loss-detail.component.html',
  styleUrls: [ './loss-detail.component.scss' ]
})
export class LossDetailComponent implements OnInit, DeactivationGuarded {
  public formModel?: AddLossDetailsForm;
  public isFormConfirmed: boolean = false;
  public claimGuid: string;
  public maxThirdPartyVehiclesInvolved: number = 3;
  public vehicleSourceTypeId: number;
  public claimDetails: any;
  public thirdPartyInsuranceCompanies: any = []
  public additionalInfoMakeList: any = []
  public lossLocationLatitude: any;
  public lossLocationLongitude: any;
  public carLocationLatitude: any;
  public carLocationLongitude: any;

  @ViewChildren('model') model;
  @ViewChildren('year') year;
  @ViewChild('accidentDate') accidentDateControl: ChqInputComponent;
  @ViewChild('state') state: ChqInputComponent;
  @ViewChildren('uploader') uploader;
  public file: any[] = [];

  public domainId: number;
  public objectId: number;
  public repairId: string;

  public currentStatus: string = '';
  public showEditDetailsButtons: boolean = false;
  public showSaveButton: boolean = false;
  public isSaveButtonClicked: boolean = false; // To Prevent showing cancle conformation popup on save button click
  public showEditDeleteButtonsClaim: boolean = true;
  public isEditAllowed: boolean = false;
  public userDetails: any = {}
  public userPermission: any;
  public canShowApproveRejectButton: boolean = false;
  public claimStatus: any = CLAIM_STATUS;

  /**
   * editing permissions
   */
  editPermissions(): boolean {
    const validStatuses = [ CLAIM_STATUS.Submitted, CLAIM_STATUS.ClaimSubmittedForApproval ];

    if (validStatuses.includes(this.currentStatus)) {
      if (!this.checkClaimPermission('claim.selfapproval')) {
        return false;
      }
    }

    if (this.claimDetails?.assigneeId) {
      // if assignee is not user id then he cannot edit unless he is a manager.
      if (this.commonService.userId !== this.claimDetails?.assigneeId && !this.checkClaimPermission('claim.selfapproval')) {
        return false;
      }
    }

    return true;
  }

  /**
   * show approve reject buttons
   */
  showApproveRejectButtons(): boolean {
    if ((this.commonService.isThirdPartyExternalGarage && this.claimDetails?.claimStatus === CLAIM_STATUS.RepairEstimation)
      || (!this.commonService.isThirdPartyExternalGarage && this.claimDetails?.claimStatus === CLAIM_STATUS.EstimateApproved)
      || (!this.commonService.isThirdPartyExternalGarage && this.claimDetails?.claimStatus === CLAIM_STATUS.EstimateRejected)) {
      return true;
    }

    return false;
  }

  /**
   * claim permission
   * @param permissionName 
   */
  checkClaimPermission(permissionName: string): boolean {

    const automotiveService = this.userPermission?.automotiveServices;
    const permissionList = automotiveService?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda claims')?.permissions;

    return this.commonService.hasPermission(permissionName, permissionList);
  }

  public saveButtonModel: ChqWidgetsButtonModel = {
    label: 'save',
    type: 'disabled',
    onclick: this.save.bind(this)
  }

  public editDetailsModel: ChqWidgetsButtonModel = {
    label: 'edit_details',
    type: 'outline',
    onclick: this.editDetails.bind(this)
  }

  public cancelButtonModel: ChqWidgetsButtonModel = {
    label: 'cancel',
    type: 'outline',
    onclick: this.cancel.bind(this)
  }

  public deleteButtonModel: ChqWidgetsButtonModel = {
    label: 'delete',
    type: 'outline-danger',
    onclick: this.delete.bind(this)
  }

  public addvehicleInvoledButtonModel: ChqWidgetsButtonModel = {
    label: 'add_vehicle_involved',
    type: 'outline',
    icon: 'plus-active',
    onclick: this.addvehicleInvoled.bind(this)
  }

  public UploadAttachmentButtonModel: ChqWidgetsButtonModel = {
    label: 'upload_attachment',
    type: 'outline',
    icon: 'attachment',
  }

  //claim status
  public rejectButtonModel: ChqWidgetsButtonModel = {
    label: 'reject',
    type: 'outline-danger',
  }

  public approveButtonModel: ChqWidgetsButtonModel = {
    label: 'approve',
    type: 'primary',
    onclick: this.approve.bind(this)
  }

  public settleClaimButtonModel: ChqWidgetsButtonModel = {
    label: 'settle_claim',
    type: 'primary',
    onclick: this.settleClaim.bind(this)
  }

  /**
 * constructor
 * @param router
 */
  constructor(private router: Router,
    public commonService: CommonService,
    private monitorService: MonitorService,
    private formBuilder: FormBuilder,
    private translateService: TranslateService,
    private calendarService: CalendarService,
    private claimsService: ClaimsService,
    private route: ActivatedRoute,
    private vehicleService: VehicleService,
    public dialog: MatDialog
  ) {
    this.route.parent?.params?.subscribe((data) => {
      this.claimGuid = this.route.parent?.snapshot.paramMap.get('id');
      this.commonService.showLoading();
      forkJoin([
        this.getThirdPartyInsuranceCompanies(),
        this.getadditionalInfoMakeList()
      ]
      ).subscribe((result) => {
        this.addvehicleInvoled()
        this.getClaimDetails()
      })
    });

    this.commonService.userProfileData.subscribe((res) => {
      if (res?.data) {
        const { userPermission } = res.data;
        this.userPermission = userPermission;
      }
    });
  }

  /**
   * Called from the navigation guard when navigating away from the page
   * Here we show the confirmation dialog
   */
  canDeactivate(): boolean | Observable<boolean> | Promise<boolean> {
    if (this.isEditAllowed && this.showSaveButton && !this.isSaveButtonClicked) {
      const dialogRef = this.commonService.openConfirmationOkDialog(this.translateService.instant('confirm_cancel'), this.translateService.instant('confirm_cancel_message'));
      return dialogRef.afterClosed().pipe(
        map((response: any) => {
          if (response) {
            return true; // Allow Navigation
          }
          return false;// Disallow Navigation
        })
      );
    } else {
      return true // Allow Navigation
    }
  }

  /**
   * on init
   */
  ngOnInit(): void {

    this.formModel = newLossDetailsForm();
    // Call setMode with text so that when calling the API field should not show as editable
    this.setMode('text')
    this.formModel = { ...this.formModel, additionalVehicleDetails: [] };

    // Set Dropdown data
    if (!this.formModel.intimationType.observable) {
      this.formModel.intimationType.observable = this.claimsService.getLookup('CLMIntimationType');
      this.formModel.intimationType.onObservableLoad = this.onObservableLoad.bind(this, 'intimationType')
    }
    if (!this.formModel.subIntimationType.observable) {
      this.formModel.subIntimationType.observable = this.claimsService.getLookup('CLMSubIntimationType');
      this.formModel.subIntimationType.onObservableLoad = this.onObservableLoad.bind(this, 'subIntimationType')
    }
    if (!this.formModel.lossImpactZone.observable) {
      this.formModel.lossImpactZone.observable = this.claimsService.getLookup('CLMLossImpactZone');
      this.formModel.lossImpactZone.onObservableLoad = this.onObservableLoad.bind(this, 'lossImpactZone')
    }
    if (!this.formModel.natureofLoss.observable) {
      this.formModel.natureofLoss.observable = this.claimsService.getLookup('CLMNatureOfLoss');
      this.formModel.natureofLoss.onObservableLoad = this.onObservableLoad.bind(this, 'natureofLoss')
    }
    if (!this.formModel.causeOfLoss.observable) {
      this.formModel.causeOfLoss.observable = this.claimsService.getLookup('CLMCauseOfLoss');
      this.formModel.causeOfLoss.onObservableLoad = this.onObservableLoad.bind(this, 'causeOfLoss')
    }
    if (!this.formModel.repairType.observable) {
      this.formModel.repairType.observable = this.claimsService.getLookup('CLMRepairType');
      this.formModel.repairType.onObservableLoad = this.onObservableLoad.bind(this, 'repairType')
    }
    if (!this.formModel.lossSeverity.observable) {
      this.formModel.lossSeverity.observable = this.claimsService.getLookup('CLMLossSeverity');
      this.formModel.lossSeverity.onObservableLoad = this.onObservableLoad.bind(this, 'lossSeverity')
    }

    if (!this.formModel.damageType.observable) {
      this.formModel.damageType.observable = this.claimsService.getLookup('CLMDamageType');
      this.formModel.damageType.onObservableLoad = this.onObservableLoad.bind(this, 'damageType')
    }
    if (!this.formModel.country.observable) {
      this.formModel.country.observable = this.claimsService.getCountries();
      this.formModel.country.onObservableLoad = this.onObservableLoad.bind(this, 'country')
    }

    const date = dayjs().toDate()
    this.formModel.lossDateTime = {
      ...this.formModel.lossDateTime,
      maxDate: date,
      value: date,
      validation: {
        name: 'lossDateTime',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required, this.isValidDateValidator(date, 'Loss date cannot be in the future') ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_date'
          } else if (error['message']) {
            return error['message']
          } else {
            return ''
          }
        }
      }
    };

    this.commonService.userProfileData.subscribe(async (res) => {
      if (res && res.data) {
        const { vehicleSourceType } = res.data;
        this.vehicleSourceTypeId = vehicleSourceType;
        this.userDetails = res.data
      }
    });
  }

  /**
   * check route params
   */
  checkRouteParams(): void {
    this.route.queryParams.subscribe((params) => {
      const codeParam = params['mode'];
      if (codeParam === 'edit' && this.isEditAllowed === true) {
        this.setMode('value');
        if (!this.claimDetails?.lossDetails?.countryID && this.userDetails?.organizationDetail?.countryId) {
          this.populateDropdownData('country', this.userDetails.organizationDetail.countryId)
        }
      } else if (this.currentStatus) {
        this.setMode('text');
      }
    });
  }

  /**
   * @returns {void}
  */
  public setMode(mode: string = 'text'): void {
    const keys = Object.keys(this.formModel);
    //this.formModel.additionalVehicleDetails;
    const keyToSkip = [ 'claimCreationDate'.toLowerCase(), 'lossSeverity'.toLowerCase() ]
    keys.forEach((key) => {
      if (key.toLowerCase() == 'additionalvehicledetails') {
        this.formModel.additionalVehicleDetails.forEach((vehicle, index) => {
          Object.keys(vehicle).forEach((k) => {
            this.formModel.additionalVehicleDetails[index][k] = {
              ...this.formModel.additionalVehicleDetails[index][k],
              mode: mode,
            };
          });
        })
      } else {
        if (!keyToSkip.includes(key.toLowerCase())) {
          this.formModel[key] = {
            ...this.formModel[key],
            mode: mode,
          };
        }
      }
    });

    if (mode === 'value') {
      this.showSaveButton = true;
      this.showEditDetailsButtons = false;
    } else {
      this.showSaveButton = false;
      this.showEditDetailsButtons = true;
    }
  }
  /**
     * @returns {void}
    */
  editDetails(): void {

    // Bug 25826: Claim Manager is able to edit Claims created from FNOL/File a Claim without assigning
    if ((this.checkClaimPermission('claim.selfapproval')
      && (this.commonService.claimWorkflowStage.includes('2Stage') || this.commonService.claimWorkflowStage.includes('3Stage'))
      && ([ CLAIM_STATUS.ClaimAuthenticated ].includes(this.currentStatus)))
    ) {
      const dialog = this.commonService.openConfirmYesNoDialogBig('', 'msg_manager_review');

      dialog.afterClosed().subscribe((data) => {
        if (data) {
          this.claimsService.updateClaimStatus(this.claimGuid, CLAIM_STATUS.ApprovalReviewInProgress).subscribe({
            next: (response) => {
              if (response) {
                this.commonService.showInfoToast(3000, `Status set to ${CLAIM_STATUS.ApprovalReviewInProgress}`);
                this.currentStatus = CLAIM_STATUS.ApprovalReviewInProgress;
                this.setMode('value');
                if (!this.claimDetails?.lossDetails?.countryID && this.userDetails?.organizationDetail?.countryId) {
                  this.populateDropdownData('country', this.userDetails.organizationDetail.countryId)
                }
              } else {
                this.router.navigate([ '/claim/my-claim' ]);
              }
            }
          });
        } else {
          this.router.navigate([ '/claim/my-claim' ]);
        }
      }
      );
    } else {
      if (this.isEditAllowed) {
        this.setMode('value');
        if (!this.claimDetails?.lossDetails?.countryID && this.userDetails?.organizationDetail?.countryId) {
          this.populateDropdownData('country', this.userDetails.organizationDetail.countryId)
        }
      }
    }
  }

  /**
   * @returns {void}
  */
  onObservableLoad(type: string, items): void {
    this.formModel[type] = { ...this.formModel[type], options: items }
    this.populateDropdownData(type)
  }

  /**
   * @returns {void}
  */
  getadditionalInfoMakeList(): Promise<any> {
    return firstValueFrom(this.vehicleService.getVehicleMake()).then((response) => {
      if (response) {
        this.additionalInfoMakeList = response || [];
      }
    }).catch((err) => {
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
   * @returns {void}
  */
  getThirdPartyInsuranceCompanies(): Promise<any> {
    return firstValueFrom(this.claimsService.getLookup('CLMThirdPartyInsuranceCompany')).then((response) => {
      if (response) {
        this.thirdPartyInsuranceCompanies = response.data || [];
      }
    }).catch((err) => {
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
   * @returns {void}
  */
  public isValidDateValidator(maxDate, message): any {
    return (control: FormControl) => {
      if (new Date(control.value) > new Date(maxDate)) {
        return { invalid: true, message };
      }
      return null;
    };
  }

  /**
   * @returns {void}
  */
  canEdit(): boolean {
    return [
      CLAIM_STATUS.Draft.toLowerCase(),
      CLAIM_STATUS.ClaimAuthenticated.toLowerCase(),
      CLAIM_STATUS.DetailsUpdated.toLowerCase(),
      CLAIM_STATUS.UploadDocuments.toLowerCase(),
      CLAIM_STATUS.UploadPhotos.toLowerCase(),
      CLAIM_STATUS.Submitted.toLowerCase(),
      CLAIM_STATUS.Assigned.toLowerCase(),
      CLAIM_STATUS.ReviewInProgress.toLowerCase(),
      CLAIM_STATUS.AssignedBack.toLowerCase(),
      CLAIM_STATUS.ApprovalReviewInProgress.toLowerCase(),
      CLAIM_STATUS.ClaimSubmittedForApproval.toLowerCase(),
    ].includes(this.currentStatus?.toLowerCase()?.trim()) ? true : false
  }

  /**
   * @returns {void}
  */
  getClaimDetails(): void {
    this.commonService.showLoading();
    firstValueFrom(this.claimsService.getClaimDetail(this.claimGuid)).then((response) => {
      this.commonService.hideLoading();
      // Set Loss date and time max date:
      if (response?.customerDetails?.expiryDate) {
        if (dayjs().isAfter(dayjs(response.customerDetails.expiryDate))) {
          this.formModel.lossDateTime = {
            ...this.formModel.lossDateTime,
            maxDate: dayjs(response.customerDetails.expiryDate).toDate(),
            value: dayjs(response.customerDetails.expiryDate).toDate(),
          }
          this.formModel.lossDateTime.formControl.setValidators(Validators.compose([ Validators.required, this.isValidDateValidator(response.customerDetails.expiryDate, 'Loss date and time can\'t be after policy expiry date.') ]))
        }
      }
      if (response) {
        this.claimDetails = response;
        this.currentStatus = response.claimStatus;
        this.isEditAllowed = this.canEdit();
        this.commonService.claimDetails.next(response);
        this.commonService.claimStatus.next(this.currentStatus);

        // This indicates the policy has just been updated from the DRAFT status to claim authenticated from Customer Details Section from FNOL
        // change to Review in Progress for Stage2 claim reviewer and Approval Review in Progress for Stage1 manager
        if (this.currentStatus == CLAIM_STATUS.ClaimAuthenticated && response.fnolId) {
          if ((this.checkClaimPermission('claim.submitforapproval') && this.commonService.claimWorkflowStage.includes('2Stage')) ||
            (this.checkClaimPermission('claim.selfapproval') && this.commonService.claimWorkflowStage.includes('1Stage'))) {
            const status = this.checkClaimPermission('claim.submitforapproval') ? CLAIM_STATUS.ReviewInProgress : CLAIM_STATUS.ApprovalReviewInProgress;
            this.updateStatusToReviewOrApprovalInProgress(status);
          } else if (this.checkClaimPermission('claim.selfapproval') && this.commonService.claimWorkflowStage.includes('2Stage')) {
            const dialog = this.commonService.openConfirmYesNoDialogBig('', 'msg_manager_review');
            dialog.afterClosed().subscribe((data) => {
              if (data) {
                this.updateStatusToReviewOrApprovalInProgress(CLAIM_STATUS.ApprovalReviewInProgress)
              }
            }
            );
          }
        }

        this.showEditDeleteButtonsClaim = this.editPermissions();
        this.canShowApproveRejectButton = this.showApproveRejectButtons();
        this.prepopulateFormData();
      }
      this.checkRouteParams()
    }).catch((err) => {
      this.commonService.hideLoading();
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
   * update status to review in progress
   */
  updateStatusToReviewOrApprovalInProgress(status: string): void {
    this.claimsService.updateClaimStatus(this.claimGuid, status).subscribe({
      next: (response) => {
        if (response) {
          this.commonService.showInfoToast(3000, `Status set to ${status}`);
          this.claimDetails.claimStatus = this.currentStatus = status;
          this.commonService.claimStatus.next(this.currentStatus);
          this.commonService.claimDetails.next(this.claimDetails);
        }
      }
    });
  }

  /**
  Handles the click event for the "Add File" button.
  @param {Event} event - The click event.
  @returns {void}
  */
  handleAddFileClick(event: any, index): void {
    this.uploader.toArray()[index].nativeElement.click()
  }

  /**
  @returns {void}
  */
  handleFileDownload(url): void {
    const urls = url.split('/');
    const fileName = urls[urls?.length - 1]?.split('?')[0];

    this.commonService.showLoading()
    this.commonService.getFileBlob(url).subscribe({
      next: (value) => {
        this.commonService.hideLoading()
        const blob = new Blob([ value.res ], { type: 'application/octet-stream' });
        saveAs(blob, fileName);
      },
      error: (err: any) => {
        this.commonService.hideLoading();
        this.monitorService.logException(err);
      }
    });
  }

  /**
  @returns {void}
  */
  handleFilePreview(url): void {
    window.open(url, '_blank');
  }
  /**
   * checks file size
   * @param blob 
   * @returns boolean
   */
  checkFileSize(img: Blob): boolean {
    this.monitorService.logEvent('checkFileSize', [ 'LossDetailComponent', 'addenda-claim', {
      fileSize: img.size
    } ]);
    if (img.size > this.commonService.maxileSize) {
      return false;
    }
    return true;
  }

  /**
   * checkx filename
   * @param filename 
   * @returns 
   */
  public isValidImageFile(filename: string): boolean {
    const regex: RegExp = new RegExp('^.*\\.[a-zA-Z]+$', 'gm');
    this.monitorService.logEvent('isValidImageFile', [ 'LossDetailComponent', 'addenda-claim', {
      fileName: filename
    } ]);
    if (regex.test(filename)) {
      const extension = filename.split('.').pop();
      return [ 'png', 'jpg', 'jpeg', 'pdf' ].includes(extension?.toLowerCase());
    }
    return false;
  }

  /**
   * handle file upload
   * @param event 
  */
  handleUpload(event: any, index): void {
    const currentFile = event.target.files || event.srcElement.files;
    this.monitorService.logEvent('handleUpload', [ 'LossDetailComponent', 'addenda-claim', {
      event
    } ]);
    if (currentFile !== null && currentFile !== '' && currentFile.length > 0) {
      if (!this.checkFileSize(currentFile[0])) {
        const message = this.translateService.instant('file_size_5mb_error_message');
        this.commonService.showToast(0, message);
        this.uploader.toArray()[index].nativeElement.value = null;
        return;
      }
      if (!this.isValidImageFile(currentFile[0].name)) {
        const message = this.translateService.instant('upload_type_not_supported');
        this.commonService.showToast(0, message);
        this.uploader.toArray()[index].nativeElement.value = null;
        return;
      }
      this.commonService.showLoading();
      this.claimsService.uploadDocument({
        claimGuid: this.claimGuid,
        files: currentFile
      }).subscribe({
        next: (result: any) => {
          this.commonService.hideLoading();
          if (result?.data?.length > 0) {
            this.file[index].push(result.data[0])
          }
        },
        error: (err) => {
          this.commonService.hideLoading();
          this.monitorService.logException(err, SeverityLevel.Error);
        }
      });
    } else {
      this.checkForFormValidation()
      return;
    }
  }

  /**
    Removes a file from the component's state.
    @param {any} event - The event object that triggered the function.
    @returns {void}
  */
  handleFileRemove(index: any, innerindex): void {
    this.commonService.openDeleteDialog(
      '',
      this.translateService.instant('attachment_delete_confirmation'),
      this.translateService.instant('delete'),
      this.translateService.instant('cancel')
    )
      .afterClosed().subscribe((data) => {
        if (data) {
          this.monitorService.logEvent('handleFileRemove', [ 'LossDetailComponent', 'addenda-claims' ]);
          this.commonService.showLoading()
          this.claimsService.removeDocument({
            documentUrl: this.file[index][innerindex].blobUrl,
          }).subscribe({
            next: (result: any) => {
              this.commonService.hideLoading();
              this.file[index].splice(innerindex, 1);
            },
            error: (err) => {
              this.commonService.hideLoading();
              this.monitorService.logException(err, SeverityLevel.Error);
            }
          });

        }
      });

  }


  /**
   * @returns {void}
  */
  prepopulateFormData(): void {
    if (this.claimDetails) {
      const lossDetails = this.claimDetails?.lossDetails || {}
      if (this.claimDetails?.claimantAtFault === true || this.claimDetails?.claimantAtFault === false) {
        this.formModel.claimantAtFault.value = this.claimDetails?.claimantAtFault ? 'At Fault' : 'Non Fault'
      }

      if (lossDetails.claimCreationDate) {
        this.formModel.claimCreationDate = { ...this.formModel.claimCreationDate, value: lossDetails.claimCreationDate }
      }
      if (lossDetails.lossDate) {
        this.formModel.lossDateTime = { ...this.formModel.lossDateTime, value: dayjs.utc(lossDetails.lossDate).local().format() }
      }

      if (lossDetails.lossLocation) {
        this.formModel.lossLocation = { ...this.formModel.lossLocation, value: lossDetails.lossLocation }
      }

      if (lossDetails.salvageAmount) {
        this.formModel.salvageAmount = { ...this.formModel.salvageAmount, value: lossDetails.salvageAmount }
      }
      if (lossDetails.isTotalLoss) {
        this.formModel.totalLoss = { ...this.formModel.totalLoss, value: 'Yes' }
      }
      if (lossDetails.lossDescription) {
        this.formModel.lossDescription = { ...this.formModel.lossDescription, value: lossDetails.lossDescription }
      }

      if (lossDetails.lossLocationLatitude) {
        this.lossLocationLatitude = lossDetails.lossLocationLatitude
      }
      if (lossDetails.lossLocationLongitude) {
        this.lossLocationLongitude = lossDetails.lossLocationLongitude
      }
      if (lossDetails.isCarAtDifferentLoc === true || lossDetails.isCarAtDifferentLoc === false) {
        this.formModel.isCarLocationDifferent.value = lossDetails.isCarAtDifferentLoc ? 'Yes' : 'No';
        this.setCarLocationValidation(this.formModel.isCarLocationDifferent.value);
      }
      if (lossDetails.policeReferenceNo) {
        this.formModel.policeReferenceNumber = { ...this.formModel.policeReferenceNumber, value: lossDetails.policeReferenceNo }
      }
      if (lossDetails.carLocation) {
        this.formModel.vehicleLocation = { ...this.formModel.vehicleLocation, value: lossDetails.carLocation }
      }

      if (lossDetails.carLocationLatitude) {
        this.carLocationLatitude = lossDetails.carLocationLatitude
      }
      if (lossDetails.carLocationLongitude) {
        this.carLocationLongitude = lossDetails.carLocationLongitude
      }

      this.populateDropdownData('state')
      this.populateDropdownData('country')
      this.populateDropdownData('intimationType')
      this.populateDropdownData('subIntimationType')
      this.populateDropdownData('natureofLoss')
      this.populateDropdownData('lossSeverity')
      this.populateDropdownData('damageType')
      this.populateDropdownData('lossImpactZone')
      this.populateDropdownData('causeOfLoss')
      this.populateDropdownData('repairType');

      if (lossDetails?.additionalLossDetails?.length > 0) {
        // Add no of additional vehicle which got from API
        lossDetails.additionalLossDetails.forEach((element, index) => {
          if (!(this.formModel.additionalVehicleDetails[index])) {
            const newVehicleDetail = newVehicleDetailsForm()
            newVehicleDetail.make.options = this.additionalInfoMakeList;
            newVehicleDetail.insuranceCompany.options = this.thirdPartyInsuranceCompanies;
            this.formModel.additionalVehicleDetails[index] = newVehicleDetail
            this.file[index] = []
          }
        })
        // Prepopulate data
        lossDetails.additionalLossDetails.forEach((element, index) => {
          if (element.vinNum) {
            this.formModel.additionalVehicleDetails[index].vehicleChasisNumber = {
              ...this.formModel.additionalVehicleDetails[index].vehicleChasisNumber,
              value: element.vinNum
            }
          }
          if (element.vehicleOwnerName) {
            this.formModel.additionalVehicleDetails[index].vehicleOwnerName = {
              ...this.formModel.additionalVehicleDetails[index].vehicleOwnerName,
              value: element.vehicleOwnerName
            };
          }
          if (element.plateNo) {
            this.formModel.additionalVehicleDetails[index].vehicleRegistrationNum = {
              ...this.formModel.additionalVehicleDetails[index].vehicleRegistrationNum,
              value: element.plateNo
            }
          }
          if (element.policyNo) {
            this.formModel.additionalVehicleDetails[index].policyNumber = {
              ...this.formModel.additionalVehicleDetails[index].policyNumber,
              value: element.policyNo
            }
          }

          if (element.isOtherVehicleFault) {
            this.formModel.additionalVehicleDetails[index].faultType = {
              ...this.formModel.additionalVehicleDetails[index].faultType,
              value: 'At Fault'
            }
          }

          if (element.insuranceCompanyName) {
            const selected = this.formModel.additionalVehicleDetails[index].insuranceCompany.options.find(ele => ele.name === element.insuranceCompanyName);
            if (selected) {
              this.formModel.additionalVehicleDetails[index].insuranceCompany = {
                ...this.formModel.additionalVehicleDetails[index].insuranceCompany,
                selectedOption: selected
              }
            }
          }

          if (element.make) {
            const selected = this.formModel.additionalVehicleDetails[index].make.options.find(ele => ele.name === element.make);
            if (selected) {
              this.formModel.additionalVehicleDetails[index].make = {
                ...this.formModel.additionalVehicleDetails[index].make,
                selectedOption: selected
              }
              this.formModel.additionalVehicleDetails[index].model = {
                ...this.formModel.additionalVehicleDetails[index].model,
                observable: this.vehicleService.getVehicleModel(selected.id, this.vehicleSourceTypeId),
                onObservableLoad: this.onObservableLoadModel.bind(this, index),
                selectedOption: {}
              }
              setTimeout(() => {
                this.model.toArray()[index].triggerObservable();
              })
            }
          }

          if (element?.lossDocuments?.length > 0) {
            this.file[index] = element.lossDocuments;
          }
        });
      }

      setTimeout(() => {
        this.checkForFormValidation();
      })
    }
  }

  /**
   * @returns {void}
  */
  onObservableLoadModel(index, items): void {
    this.formModel.additionalVehicleDetails[index].model = { ...this.formModel.additionalVehicleDetails[index].model, options: items }
    if (this.claimDetails?.lossDetails?.additionalLossDetails[index]?.model) {
      const selected = items.find(ele => ele.modelDescription === this.claimDetails.lossDetails.additionalLossDetails[index].model)
      if (selected) {
        this.formModel.additionalVehicleDetails[index].model = { ...this.formModel.additionalVehicleDetails[index].model, selectedOption: selected }
        this.formModel.additionalVehicleDetails[index].year = {
          ...this.formModel.additionalVehicleDetails[index].year,
          observable: this.vehicleService.getVehicleYearByModel(selected.id, this.vehicleSourceTypeId),
          onObservableLoad: this.onObservableLoadYear.bind(this, index),
          selectedOption: {}
        }
        setTimeout(() => {
          this.year.toArray()[index].triggerObservable()
        })
      }
    }
  }

  /**
   * @returns {void}
  */
  onObservableLoadYear(index, items): void {
    this.formModel.additionalVehicleDetails[index].year = { ...this.formModel.additionalVehicleDetails[index].year, options: items }
    if (this.claimDetails?.lossDetails?.additionalLossDetails[index]?.year) {
      const selected = items.find(ele => ele.name === this.claimDetails.lossDetails.additionalLossDetails[index].year)
      if (selected) {
        this.formModel.additionalVehicleDetails[index].year = { ...this.formModel.additionalVehicleDetails[index].year, selectedOption: selected }
      }
    }
  }


  /**
   * @returns {void}
  */
  populateDropdownData(dropdownType: string, countryId: string = ''): void {
    switch (dropdownType) {
    case 'state': {
      if (this.claimDetails?.lossDetails?.stateId && this.formModel.state?.options?.length > 0) {
        const selectedState = this.formModel.state?.options.find(ele => ele.id === this.claimDetails.lossDetails.stateId);
        if (selectedState) {
          this.formModel.state = { ...this.formModel.state, selectedOption: selectedState }
        }
      }
      break;
    }
    case 'country': {
      if ((this.claimDetails?.lossDetails?.countryID || countryId) && this.formModel.country?.options?.length > 0) {
        const selectedCountry = this.formModel.country?.options.find(ele => ele.id === this.claimDetails.lossDetails.countryID || ele.id === countryId);
        if (selectedCountry) {
          this.formModel.country = { ...this.formModel.country, selectedOption: selectedCountry }
          this.formModel.state = {
            ...this.formModel.state,
            observable: this.claimsService.getStates(selectedCountry.id),
            onObservableLoad: this.onObservableLoad.bind(this, 'state'),
            selectedOption: {}
          }
          setTimeout(() => {
            this.state.triggerObservable();
          }, 100)
        }
      }
      break;
    }
    case 'intimationType': {
      if (this.claimDetails?.lossDetails?.intimationTypeID && this.formModel.intimationType?.options?.length > 0) {
        const selectedintimationType = this.formModel.intimationType?.options.find(ele => ele.id === this.claimDetails.lossDetails.intimationTypeID);
        if (selectedintimationType) {
          this.formModel.intimationType = { ...this.formModel.intimationType, selectedOption: selectedintimationType }
        }
      }
      break;
    }
    case 'subIntimationType': {
      if (this.claimDetails?.lossDetails?.subIntimationTypeId && this.formModel.subIntimationType?.options?.length > 0) {
        const selectedSubintimationType = this.formModel.subIntimationType?.options.find(ele => ele.id === this.claimDetails.lossDetails.subIntimationTypeId);
        if (selectedSubintimationType) {
          this.formModel.subIntimationType = { ...this.formModel.subIntimationType, selectedOption: selectedSubintimationType }
        }
      }
      break;
    }
    case 'natureofLoss': {
      if (this.claimDetails?.lossDetails?.natureOfLossId && this.formModel.natureofLoss?.options?.length > 0) {
        const selected = this.formModel.natureofLoss?.options.find(ele => ele.id === this.claimDetails.lossDetails.natureOfLossId);
        if (selected) {
          this.formModel.natureofLoss = { ...this.formModel.natureofLoss, selectedOption: selected }
        }
      }
      break;
    }
    case 'lossSeverity': {
      if (this.claimDetails?.lossDetails?.lossSeverityId && this.formModel.lossSeverity?.options?.length > 0) {
        const selected = this.formModel.lossSeverity?.options.find(ele => ele.id === this.claimDetails.lossDetails.lossSeverityId);
        if (selected) {
          this.formModel.lossSeverity = { ...this.formModel.lossSeverity, selectedOption: selected }
        }
      }
      break;
    }
    case 'lossImpactZone': {
      if (this.claimDetails?.lossDetails?.lossImpactZoneId && this.formModel.lossImpactZone?.options?.length > 0) {
        const selected = this.formModel.lossImpactZone?.options.find(ele => ele.id === this.claimDetails.lossDetails.lossImpactZoneId);
        if (selected) {
          this.formModel.lossImpactZone = { ...this.formModel.lossImpactZone, selectedOption: selected }
        }
      }
      break;
    }
    case 'causeOfLoss': {
      if (this.claimDetails?.lossDetails?.causeOfLoss && this.formModel.causeOfLoss?.options?.length > 0) {
        const selected = this.formModel.causeOfLoss?.options.find(ele => ele.name === this.claimDetails.lossDetails.causeOfLoss);
        if (selected) {
          this.formModel.causeOfLoss = { ...this.formModel.causeOfLoss, selectedOption: selected }
        }
      }
      break;
    }
    case 'repairType': {
      if (this.claimDetails?.lossDetails?.repairTypeId && this.formModel.repairType?.options?.length > 0) {
        const selected = this.formModel.repairType?.options.find(ele => ele.id === this.claimDetails.lossDetails.repairTypeId);
        if (selected) {
          this.formModel.repairType = { ...this.formModel.repairType, selectedOption: selected }
        }
      }
      break;
    }
    case 'damageType': {
      if (this.claimDetails?.lossDetails?.damageTypeId && this.formModel.damageType?.options?.length > 0) {
        const selected = this.formModel.damageType?.options.find(ele => ele.id === this.claimDetails.lossDetails.damageTypeId);
        if (selected) {
          this.formModel.damageType = { ...this.formModel.damageType, selectedOption: selected }
        }
      }
      break;
    }
    default: {
      return
    }

    }
    this.checkForFormValidation()
  }


  /**
   * on form update
   * @param output o
   */
  async onFormUpdate(output: IControlValue): Promise<void> {
    if (output.type === 'select') {
      this.formModel[output.name] = {
        ...this.formModel[output.name],
        selectedOption: output.value
      }

      // GET states of selected country
      if (output.name === 'country' && output.value?.id) {
        this.formModel.state = {
          ...this.formModel.state,
          observable: this.claimsService.getStates(output.value.id),
          selectedOption: {}
        }
        setTimeout(() => {
          this.state.triggerObservable();
        }, 100)
      }

    } else if (output.name === 'lossLocation') {
      // If user selected from dropdown output.value will be complete object if user type it and not select from dropdown value will be string
      if (output.value instanceof Object) {
        this.formModel[output.name].value = output.value.name + ', ' + output.value.formatted_address
        this.lossLocationLatitude = output.value.geometry.location.lat()
        this.lossLocationLongitude = output.value.geometry.location.lng()
      } else {
        this.formModel[output.name].value = output.value;
      }
    } else if (output.name === 'vehicleLocation') {
      // If user selected from dropdown output.value will be complete object if user type it and not select from dropdown value will be string
      if (output.value instanceof Object) {
        this.formModel[output.name].value = output.value.name + ', ' + output.value.formatted_address
        this.carLocationLatitude = output.value.geometry.location.lat()
        this.carLocationLongitude = output.value.geometry.location.lng()
      } else {
        this.formModel[output.name].value = output.value;
      }
    } else {
      this.formModel[output.name].value = output.value;
      if (output.name === 'totalLoss') {
        this.setSalvageAmountValidation(output.value)
      }
      if (output.name === 'isCarLocationDifferent') {
        this.setCarLocationValidation(output.value)
      }
    }
    this.checkForFormValidation();
  }

  /**
   * on form update
   * @param output o
   */
  async onOtherVehicleFormUpdate(output: IControlValue, index: number): Promise<void> {
    if (output.type === 'select') {
      this.formModel.additionalVehicleDetails[index][output.name] = {
        ...this.formModel.additionalVehicleDetails[index][output.name],
        selectedOption: output.value
      }
      if (output.name === 'make' && output.value?.id) {
        this.formModel.additionalVehicleDetails[index].model = {
          ...this.formModel.additionalVehicleDetails[index].model,
          observable: this.vehicleService.getVehicleModel(output.value.id, this.vehicleSourceTypeId),
          selectedOption: {}
        }
        setTimeout(() => {
          this.model.toArray()[index].triggerObservable();
        })

      } else if (output.name === 'model' && output.value?.id) {
        this.formModel.additionalVehicleDetails[index].year = {
          ...this.formModel.additionalVehicleDetails[index].year,
          observable: this.vehicleService.getVehicleYearByModel(output.value?.id, this.vehicleSourceTypeId),
          selectedOption: {}
        }
        setTimeout(() => {
          this.year.toArray()[index].triggerObservable()
        })
      }
    } else {
      this.formModel.additionalVehicleDetails[index][output.name].value = output.value;
    }
    this.checkForFormValidation();
  }

  /**
   * check for form validation and set button type
   * @returns {void}
  */
  checkForFormValidation(): void {
    if (this.isFormValid()) {
      this.saveButtonModel.type = 'primary';
    } else {
      this.saveButtonModel.type = 'disabled';
    }
  }

  /**
   * check for form validation
   * @returns {void}
   */
  isFormValid(): boolean {
    let isValid = true;
    if (
      !this.formModel.lossDateTime?.formControl?.valid
      || !this.formModel.intimationType.selectedOption
      || !this.formModel.subIntimationType.selectedOption
      || !this.formModel.natureofLoss.selectedOption
      || !this.formModel.lossLocation?.formControl?.valid
      || !this.lossLocationLatitude
      || !this.lossLocationLongitude
      || !this.formModel.causeOfLoss.selectedOption
      || !this.formModel.salvageAmount?.formControl?.valid
      || (!this.formModel.salvageAmount.value && this.formModel.totalLoss.value === 'Yes')
      || ((!this.formModel.vehicleLocation.value || !this.carLocationLatitude || !this.carLocationLongitude) && this.formModel.isCarLocationDifferent.value === 'Yes')
      || !this.formModel.policeReferenceNumber.value
      || !this.formModel.policeReferenceNumber?.formControl?.valid
    ) {
      isValid = false;
    }

    this.formModel.additionalVehicleDetails.forEach((form) => {
      if (
        !form.vehicleRegistrationNum.formControl?.valid
        || !form.vehicleChasisNumber.formControl.valid
        || !form.vehicleOwnerName.formControl.valid
        || !form.policyNumber.formControl.valid
      ) {
        isValid = false;
      }
    })
    return isValid;
  }

  /**
   * setSalvageAmountValidation
   */
  setSalvageAmountValidation(value: string): void {
    if (value === 'Yes') {
      this.formModel.salvageAmount.required = true;
      this.formModel.salvageAmount.disabled = false;
      this.formModel.salvageAmount.formControl.setValidators(Validators.compose([ Validators.required, Validators.pattern(/^[0-9]+(\.[0-9]{1,2})?$/) ]))
    } else {
      this.formModel.salvageAmount.required = false
      this.formModel.salvageAmount.disabled = true;
      this.formModel.salvageAmount.formControl.setValidators(Validators.compose([ Validators.pattern(/^[0-9]+(\.[0-9]{1,2})?$/) ]))
    }
    this.checkForFormValidation();
  }

  /**
   * setCarLocationValidation
   */
  setCarLocationValidation(value: string): void {
    if (value === 'Yes') {
      this.formModel.vehicleLocation.required = true;
      this.formModel.vehicleLocation.disabled = false;
    } else {
      this.formModel.vehicleLocation = { ...this.formModel.vehicleLocation, required: false, disabled: true, value: '' }
      this.carLocationLatitude = '';
      this.carLocationLongitude = '';
    }
    this.checkForFormValidation();
  }


  /**
   * submit form
   */
  async save(): Promise<void> {
    if (!this.isFormValid()) {
      return
    }

    const additionalVehicleDetails = [];
    this.formModel.additionalVehicleDetails.forEach((form, index) => {
      const vDetails: any = {
        vehicleOwnerName: form.vehicleOwnerName.value,
        insuranceCompanyName: form.insuranceCompany.selectedOption?.name,
        plateNo: form.vehicleRegistrationNum.value,
        policyNo: form.policyNumber.value,
        vinNum: form.vehicleChasisNumber.value,
        make: form.make.selectedOption?.name || '',
        model: form.model.selectedOption?.modelDescription || '',
        year: form.year.selectedOption?.name || '',
        isOtherVehicleFault: form.faultType.value === 'At Fault' ? true : false,
        lossDocuments: this.file[index]
      }
      if (this.claimDetails?.lossDetails?.additionalLossDetails?.[index]) {
        vDetails.id = this.claimDetails.lossDetails.additionalLossDetails[index].id;
        vDetails.vehicleGuid = this.claimDetails.lossDetails.additionalLossDetails[index].vehicleGuid;
        vDetails.vehicleType = this.claimDetails.lossDetails.additionalLossDetails[index].vehicleType;
        vDetails.claimId = this.claimDetails.lossDetails.additionalLossDetails[index].claimId;
      }
      additionalVehicleDetails.push(vDetails)
    });
    const lossDate: any = this.formModel.lossDateTime.value || ''
    const lossDetails = {
      lossDate: lossDate ? dayjs(lossDate).local().utc().format() : null,
      claimCreationDate: this.claimDetails?.lossDetails?.claimCreationDate,
      stateId: this.formModel.state.selectedOption?.id,
      // cityId: this.formModel.city.selectedOption?.id,
      intimationTypeID: this.formModel.intimationType.selectedOption?.id,
      subIntimationTypeId: this.formModel.subIntimationType.selectedOption?.id,
      damageTypeId: this.formModel.damageType?.selectedOption?.id,
      natureOfLossId: this.formModel.natureofLoss.selectedOption?.id,
      lossSeverityId: this.claimDetails?.lossDetails?.lossSeverityId,
      lossImpactZoneId: this.formModel.lossImpactZone.selectedOption?.id,
      lossLocation: this.formModel.lossLocation.value,
      causeOfLoss: this.formModel.causeOfLoss.selectedOption?.name,
      isTotalLoss: this.formModel.totalLoss.value === 'Yes' ? true : false,
      salvageAmount: this.formModel.salvageAmount.value ? Number(this.formModel.salvageAmount.value) : 0,
      repairTypeId: this.formModel.repairType.selectedOption?.id,
      lossDescription: this.formModel.lossDescription.value,
      additionalLossDetails: additionalVehicleDetails,
      lossLocationLatitude: '' + this.lossLocationLatitude, // Convert to string by appending empty string
      lossLocationLongitude: '' + this.lossLocationLongitude,
      countryID: this.formModel.country.selectedOption?.id,
      isCarAtDifferentLoc: this.formModel.isCarLocationDifferent.value ? this.formModel.isCarLocationDifferent.value === 'Yes' ? true : false : null,
      carLocation: this.formModel.vehicleLocation.value || '',
      policeReferenceNo: this.formModel.policeReferenceNumber.value || null,
      carLocationLatitude: '' + this.carLocationLatitude,
      carLocationLongitude: '' + this.carLocationLongitude
    }
    this.commonService.showLoading();
    // this.claimDetails.customerDetails.categoryId = 1
    firstValueFrom(this.claimsService.updateClaimDetails({
      ...this.claimDetails,
      claimantAtFault: this.formModel.claimantAtFault.value ? this.formModel.claimantAtFault.value == 'At Fault' ? true : false : null,
      lossDetails: lossDetails
    })).then(async (response) => {
      this.commonService.hideLoading();
      if (response && this.currentStatus?.toLowerCase() == 'claimauthenticated') {
        await firstValueFrom(this.claimsService.updateClaimStatus(this.claimGuid, 'DetailsUpdated')).then((response) => {
          console.log(response);
        });
      }
      this.isSaveButtonClicked = true;
      this.router.navigate([ `/claim/claim-detail/${this.claimGuid}/document` ]);
    }).catch((err) => {
      this.commonService.hideLoading();
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
   * hide dropdown
   */
  resetFrom(): void {
    const keys = Object.keys(this.formModel);
    this.formModel.additionalVehicleDetails;
    keys.forEach((key) => {
      if (key.toLowerCase() !== 'additionalvehicledetails') {
        if (key === 'lossDateTime') {
          this.formModel[key] = {
            ...this.formModel[key],
            value: dayjs().toDate(),
          };
        } else if (key === 'totalLoss') {
          this.formModel[key] = {
            ...this.formModel[key],
            value: 'No',
          };
        } else {
          this.formModel[key] = {
            ...this.formModel[key],
            value: '',
            selectedOption: {}
          };
        }
      }
    });
    this.formModel.additionalVehicleDetails = [];
    this.addvehicleInvoled();
    this.lossLocationLatitude = '';
    this.lossLocationLongitude = ''
  }

  /**
   * Cancel booking
   */
  cancel(): void {
    const dialogRef = this.commonService.openConfirmationOkDialog(this.translateService.instant('confirm_cancel'), this.translateService.instant('confirm_cancel_message'));
    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        this.resetFrom();
        this.prepopulateFormData();
        this.setMode('text')
      }
    });
  }

  /**
  * delete booking
  */
  delete(): void {
    // console.log('delete');
  }

  /**
  * Cupload Registration Card
  */
  addvehicleInvoled(): void {
    const newVehicleDetail = newVehicleDetailsForm()
    newVehicleDetail.make.options = this.additionalInfoMakeList;
    newVehicleDetail.insuranceCompany.options = this.thirdPartyInsuranceCompanies;
    this.file.push([])
    this.formModel.additionalVehicleDetails.push(newVehicleDetail)
  }

  //status thing
  /**
    Opens a dialog box for the reject claim operation component with the specified data and panel class.
    @returns {void}
  */
  openRejectClaimPopup(data?: any): void {
    const dialogRef = this.dialog.open(RejectClaimDialogComponent, {
      data: {
        'claimGuid': this.claimGuid
      },
      height: 'auto',
      panelClass: 'operation-panel',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if (response) {
          this.currentStatus = CLAIM_STATUS.Rejected;
          if (this.claimDetails) {
            this.claimDetails.claimStatus = this.currentStatus;
            this.commonService.claimDetails.next(this.claimDetails);
          }

          this.canShowApproveRejectButton = this.showApproveRejectButtons();

          this.commonService.claimStatus.next(this.currentStatus);
        }
      }
    });
  }

  /**
         * Approve claim
         */
  approve(): void {
    const dialogRef = this.commonService.openConfirmYesNoDialogBig(this.translateService.instant('confirm_action'), this.translateService.instant('msg_approve_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimsService.updateClaimStatus(this.claimGuid, 'Approved', 'Claim Approved').subscribe({
          next: (response) => {
            if (response) {
              this.currentStatus = this.claimDetails.claimStatus = CLAIM_STATUS.Approved;
              this.commonService.claimDetails.next(this.claimDetails);
              this.commonService.claimStatus.next(this.currentStatus);
              this.canShowApproveRejectButton = this.showApproveRejectButtons();
            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }

  /**
    * settleClaim
  */
  settleClaim(): void {
    const dialogRef = this.commonService.openConfirmYesNoDialogBig(this.translateService.instant('confirm_action'), this.translateService.instant('msg_settle_claim'));
    dialogRef.afterClosed().subscribe((action: boolean) => {
      if (action) {
        this.commonService.showLoading();
        this.claimsService.updateClaimStatus(this.claimGuid, 'Completed', 'Settled').subscribe({
          next: (response) => {
            if (response) {
              this.claimDetails.claimStatus = this.currentStatus = CLAIM_STATUS.Complete;
              this.commonService.claimDetails.next(this.claimDetails);
              this.commonService.claimStatus.next(this.claimDetails.claimStatus);
            }
            this.commonService.hideLoading();
          },
          error: () => {
            this.commonService.hideLoading();
          }
        });
      }
    });
  }
}

