import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ChqWidgetsInputModel, IControlValue } from 'src/app/model/chq-widgets-input-model';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor/monitor.service';

@Component({
  selector: 'app-settle-claim-dialog',
  templateUrl: './settle-claim-dialog.component.html',
  styleUrls: [ './settle-claim-dialog.component.scss' ]
})
export class SettleClaimDialogComponent {
  public sendButton: ChqWidgetsButtonModel;
  public attachmentButton: ChqWidgetsButtonModel;
  isUpdate:boolean=false;

  public subjectInputModel:ChqWidgetsInputModel = {
    placeHolder: 'subject',
    label: 'subject',
    name: 'subject',
    value: '',
    type: 'text',
  };

  public messageModel:ChqWidgetsInputModel = {
    placeHolder: 'message',
    label: 'message',
    name: 'message',
    value: '',
    type: 'textarea',
    maxLength: 100
  };

  public notes = '';
  


  /**
   * constructor
   * @param dialogRef 
   * @param data 
   * @param changeDedectionRef 
   */
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<SettleClaimDialogComponent>,
    private monitorService:MonitorService,
    public commonService:CommonService
  ) {
  }
  
  /**
   * handle close
   */
  continue():void{
    this.monitorService.logEvent('continue', [ 'SettleClaimDialogComponent', 'addenda-repair' ]);
    this.dialogRef.close({ 'comment': this.messageModel.value });
  }

  /**
   * on form update
   * @param output o
   */
  onFormUpdate(output: IControlValue): void {
    this.messageModel.value = output.value;
    // if(this.messageModel.value.toString().trim()){
    //   this.sendButton = { ...this.sendButton, type: 'primary' };
    // }else{
    //   this.sendButton = { ...this.sendButton, type: 'disabled' };
    // }
  }

  /**
   * close
   */
  close():void{
    this.dialogRef.close();
  }


  /**
   * ng on init
   */
  ngOnInit(): void {
    this.monitorService.logEvent('ngOnInit', [ 'SettleClaimDialogComponent', 'addenda-repair' ]);
    this.sendButton = {
      label: 'send',
      type: 'disabled',
      onclick: this.continue.bind(this),
      icon: 'send' 
    };

    this.attachmentButton = {
      label: 'attachment',
      type: 'primary',
      onclick: this.continue.bind(this),
      icon: 'attachment' 
    }
   
    this.messageModel = { ...this.messageModel, value: this.data?.notes, disabled: this.data?.hideUpdate };

  }
}
