import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor/monitor.service';

@Component({
  selector: 'app-customer-policy-details-dialog',
  templateUrl: './customer-policy-details-dialog.component.html',
  styleUrls: [ './customer-policy-details-dialog.component.scss' ]
})
export class CustomerPolicyDetailsDialogComponent {

  public yesButton: ChqWidgetsButtonModel = {
    label: 'yes',
    type: 'primary',
    onclick: this.handleYes.bind(this)
  }

  public noButton: ChqWidgetsButtonModel = {
    label: 'no',
    type: 'outline',
    onclick: this.handleNo.bind(this)
  }

  /**
   * constructor
   * @param dialogRef 
   * @param data 
   * @param changeDedectionRef 
   */
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<CustomerPolicyDetailsDialogComponent>,
    private monitorService: MonitorService,
    public commonService: CommonService
  ) {
  }

  /**
   * handle clicked on No
   */
  handleNo(): void {
    this.monitorService.logEvent('handleNo', [ 'CustomerPolicyDetailsDialogComponent', 'addenda-claim' ]);
    this.close();
  }

  /**
   * handle clicked on Yes
   */
  handleYes(): void {
    this.monitorService.logEvent('handleYes', [ 'CustomerPolicyDetailsDialogComponent', 'addenda-claim' ]);
    this.dialogRef.close(true);
  }

  /**
   * close
   */
  close(): void {
    this.dialogRef.close();
  }

}

